import React from 'react';
import PropTypes from 'prop-types';
import { Root, View, Panel , FormLayout, Input, Select, Checkbox, Button, PanelHeader, Group, Header, Cell, InfoRow} from '@vkontakte/vkui';

const Home = ({ id, go}) => (
	<Panel id={id}>
		<PanelHeader>Расчет стоимости пошива</PanelHeader>
		<FormLayout>
            <Group  header={<Header mode="secondary">Размер одной шторы</Header>}>
	            	<Cell>
	            		<div style={{ display: 'flex' }}>
	            			<Input id="width" type="number" placeholder="Ширина(см)" />
	            			<Input id="height" type="number" placeholder="Высота(см)" />
	            		</div>
	            	</Cell>
            </Group>
            <Input top="Количество штор" id="count" defaultValue="2"/>

            <Select top="Ширина ленты" id="band" placeholder="Выберите ленту" defaultValue="4 см">
              <option value="6">6 см</option>
              <option value="4">4 см</option>
            </Select>

            <Checkbox id="isHemmingBottom">Подшивать низ</Checkbox>
            <div style={{ display: 'flex' }}>
            	<Button size="l" onClick={calc}>Рассчитать</Button>
            	<InfoRow header="Стоимость пошива">
		        	<div id="cost">0 р.</div>
		        </InfoRow>
        	</div>
          </FormLayout>
	</Panel>
);

const calc = e => {
	var width = Number(document.getElementById("width").value)
	var height = Number(document.getElementById("height").value)
	var count = Number(document.getElementById("count").value)
	var band = document.getElementById("band").value
	var isHemmingBottom = document.getElementById("isHemmingBottom").checked
	
	var cost = (width + height * 2 + (band === "4" ? width : width * 2) + (isHemmingBottom ? width : 0)) * 3 * count;
	document.getElementById("cost").innerHTML = cost + " р.";
};

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired
};

export default Home;
